// BASIC
import React, {Component} from 'react';
import styled, {css} from 'styled-components'
import {Link} from 'react-router-dom'
// CONFIG
import {db, au, fu, st} from '../../Config/firebase'
// COMPONENTS
import Achievements from './components/Achievements'
// ICONS
import {FaPencilAlt} from 'react-icons/fa'
// KEYFRAMES
import { editOpacity } from '../Styles/Keyframes'
import points from './BlankPointsObject';

const UserProfileSite = styled.div`
	width: 100vw;
	height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
`
const LoginElement = styled.div`
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	color: var(--color-primary);
	width: 100vw;
	/* min-width: 540px; */
	/* min-height: 720px; */
	padding: 20px;
`
const LoginTitle = styled.header`
	font-size: 35px;
	font-weight: bold;
	text-align: center;
	margin: 30px 0;
	@media(min-width: 720px) {
		font-size: 50px;
	}
	@media(max-height: 600px) {
		margin: 15px 0 0 0;
		font-size: 35px;
	}
	@media(max-height: 450px) {
		margin: 30px 0;
	}
`
const AddAdmin = styled.form`
	display: none;
	${props =>
			props.preview &&
			css`
				display: block;
	`};
`
const UserContent = styled.div`
	display: none;
	text-align: center;
	${props =>
			props.preview &&
			css`
				display: block;
	`};
`
const Avatar = styled.div`
	width: 250px;
	height: 250px;
	position: relative;
	border-radius: 100%;
	overflow: hidden;
`
const AvatarImage = styled.img`
	display: block;
	width: 100%;
	height: 100%;
	
`
const Edit = styled(FaPencilAlt)`
	display: none;
	position: absolute;
	top: 0;
	left: 0;
	width: 250px;
	height: 250px;
	padding: 100px;
	background-color: var(--color-dark);
	animation: ${editOpacity} 0.2s ease;
	cursor: pointer;
	${Avatar}:hover & {
		display: block;
		opacity: 0.5;
	}
`
const LogIn = styled.button`
	position: relative;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	line-height: 150%;
	min-width: 180px;
	font-size: 20px;
	padding: 10px 20px;
	margin-top: 20px;
	background-color: transparent;
	border-radius: 20px;
	border: 2px solid ${props => `var(--color-${props.color})`};
	color: var(--color-primary);
	transition: all 0.5s ease;
	overflow: hidden;
	::before {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: ${props => `var(--color-${props.color})`};
		opacity: 0.5;
		filter: blur(5px);
		transform: translateX(-250px) skewX(-15deg);
		z-index: 2;
	}
	:hover {
		cursor: pointer;
		background-color: ${props => `var(--color-${props.color})`};
		color: ${props => props.color === 'main' || props.color === 'decorative' ? 'var(--color-bg)' : 'var(--color-light)'};
		::before {
			transform: translate(250px) skewX(-15deg);
			opacity: 0.6;
			transition: all 0.7s ease;
		}
	}
	:focus {
		outline: none
	}
`
class UserProfile extends Component {
	state = {
		nick: '',
		avatar: 'https://fakeimg.pl/250x250/?text=Avatar',
		email: '',
		isLoggedIn: false,
		isAdmin: false,
		uid: '',
		points: null,
		previewUserAchievements: false,
		percentage: 0
	}
	componentDidMount() {
		au.onAuthStateChanged(user => {
			if(user) {
				console.log(user);
				// this.setState({user: {
				// 	isLoggedIn: true,
				// 	email: user.email
				// }});
				this.setState({
					isLoggedIn: true,
					email: user.email
				})
				user.getIdTokenResult().then(idTokenResult => {
					if (idTokenResult.claims.admin) {
						this.setState({isAdmin: true})
					}
					else {
						this.setState({isAdmin: false})
					}
					user.admin = idTokenResult.claims.admin
				})
				db.collection('users').doc(user.uid).onSnapshot(snap => {
					const {nick, biogram, uid} = snap.data().info;
					this.setState({nick, biogram, uid})
				});
			}
			else {
				this.props.history.push(`/login`);
			}
		})
	}
	addAdminCloudFunction = (e) => {
		e.preventDefault();
		const adminEmail = document.getElementById('adminEmail').value;
		const addAdminRole = fu.httpsCallable('addAdminRole');
		addAdminRole({email: adminEmail}).then(result => {
			console.log(result);
		})
	}
	logOut = () => {
		au.signOut();
		this.props.history.push(`/login`);
	}
	editAvatar = () => {
		alert("Edycja zdjęcia")
	}
	handleUserAchievements = () => {
		this.state.previewUserAchievements ? this.setState({previewUserAchievements: false}) : this.setState({previewUserAchievements: true})
		
	}
	handleChange = (e) => {
		const file = e.target.files[0];
		const storageRef = st.ref(`photos/${file.name}`);
		const task = storageRef.put(file);
		task.on('state_changed',
		(snap) => {
			this.setState({percentage: (snap.bytesTransferred / snap.totalBytes * 100)});
		}, err => {
			console.log(err);
		}, () => {
			st.ref('photos').child(file.name).getDownloadURL().then(url => {
				console.log(url);
				// db.collection('users').doc(this.state.user.)
			})
		})
	}
	render() {
		return (
			<UserProfileSite>
				<progress value={this.state.percentage} max="100" id="uploader">0%</progress>
				<input type="file" onChange={this.handleChange} id="fileButton"/>
					<LoginElement>
						<LoginTitle>
							Witaj {this.state.nick}!
						</LoginTitle>
						<p>{this.state.biogram}</p>
						<AddAdmin onSubmit={this.addAdminCloudFunction} className="admin" preview={this.state.isAdmin}>
							<input type="email" placeholder="User email" id="adminEmail" required />
							<button>Dodaj uprawnienia</button>
						</AddAdmin>
						<UserContent preview={this.state.isLoggedIn}>
							<Avatar>
								<AvatarImage src={this.state.avatar} alt="User Image"/>
								<Edit onClick={this.editAvatar} />
							</Avatar>
							<div>Wygrane rundy: </div>
							<div>Doświadczenie: {!this.state.points ? '-' : this.state.points.experience} XP</div>
						</UserContent>
						<button onClick={this.handleUserAchievements}>Zobacz swoje wyniki</button>
						<button>Przejdź do tablicy wyników</button>
						<Link to='/'>Wróć do strony głównej</Link>
						<LogIn color="main" onClick={this.logOut}>Wyloguj się</LogIn>
					</LoginElement>
					{/* <Achievements preview={this.state.previewUserAchievements} points={this.state.points} back={this.handleUserAchievements} /> */}
			</UserProfileSite>
		);
	}
}

export default UserProfile;
