// BASIC
import React, { Component } from 'react'
import styled, {css, keyframes} from 'styled-components'
// IMAGES
import plus from '../../../Data/Icons/plus.svg'
import minus from '../../../Data/Icons/minus.svg'
import division from '../../../Data/Icons/division.svg'
import cancel from '../../../Data/Icons/cancel.svg'
import one from '../../../Data/Icons/one.svg'
import two from '../../../Data/Icons/two.svg'
import three from '../../../Data/Icons/three.svg'
// ICONS
import {FaReply} from 'react-icons/fa'

const fade = keyframes`
	from {
		opacity: 0;
		transform: scaleX(0.8);
	}
	to {
		opacity: 1;
		transform: scaleX(1);
	}
`
const hide = keyframes`
	from {
		opacity: 1;
		transform: scaleX(1);
	}
	to {
		opacity: 0;
		transform: scaleX(0.8);
	}
`
const SettingsComponent = styled.div`
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: none;
	justify-content: flex-start;
	align-items: center;
	flex-direction: column;
	padding: 20px;
	background-color: var(--color-dark);
	animation: ${fade} 0.3s ease;
	${props =>
		props.preview &&
		css`
			display: flex;
		`
	};
	${props =>
		props.hide &&
		css`
			animation: ${hide} 0.3s both;
		`
	};
`
const Title = styled.h3`
	text-align: center;
	margin: 30px 0;
`
const Section = styled.div`
	display: flex;
	flex-direction: column;
	height: calc(100% - 50px);
	overflow-y: scroll;
	::-webkit-scrollbar {
		width: 0;
	}
`
const CheckBox = styled.div`
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
`
const Choise = styled.img`
	width: 60px;
	height: 60px;
	margin: 10px;
	padding: 0;
	:hover {
		cursor: pointer;
	}
`
const Back = styled(FaReply)`
	display: block;
	margin-top: 20px;
	:hover {
		cursor: pointer;
	}
`

class Settings extends Component {
	state = {
		hide: false
	}
	back = () => {
		this.setState({hide: true});
		setTimeout(() => {
			this.props.onBack();
			this.setState({hide: false});
		}, 300);
	}
	render() {
		return (
			<SettingsComponent preview={this.props.show} hide={this.state.hide}>
				{/* <h2>Ustawienia</h2> */}
				<Section>
					<Title>Trudność:</Title>
					<CheckBox>
						<Choise id="easy" src={one} alt="one" title="Łatwy" />
						<Choise id="medium" src={two} alt="two" title="Średni" />
						<Choise id="hard" src={three} alt="three" title="Trudny" />
					</CheckBox>
					<Title>Działanie:</Title>
					<CheckBox>
						<Choise id="addition " src={plus} alt="plus" title="Dodawanie" />
						<Choise id="subtraction " src={minus} alt="minus" title="Odejmowanie" />
						<Choise id="multiplication" src={cancel} alt="cancel" title="Mnożenie" />
						<Choise id="division " src={division} alt="division" title="Dzielenie" />
					</CheckBox>
					<Title>Ujemne znaki:</Title>
					<CheckBox>
						<Choise id="submit" src={division} alt="submit" title="Potwierdź" />
						<Choise id="cancel" src={cancel} alt="cancel" title="Cofnij" />
					</CheckBox>
				</Section>
				<Back onClick={this.back} />
				{/* <Back onClick={this.showSettings} /> */}
			</SettingsComponent>
		);
	}
}

export default Settings