// BASIC
import React, { Component } from 'react'
import styled, {css, keyframes} from 'styled-components'
import * as shuffle from 'fisher-yates'
// FIREBASE
import {db, au} from '../../Config/firebase'
// ICONS
import {MdSettings} from 'react-icons/md'
// COMPONENTS
import Preloader from '../Preloader'
import LoginAd from './components/LoginAd'
import Settings from './components/Settings'
/*eslint-disable no-eval */

const CalcAppComponent = styled.div`
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-direction: column;
	width: 100vw;
	height: 100%;
	height: calc(var(--vh, 1vh) * 100);
	padding: 30px;
	font-size: 35px;
	letter-spacing: 3px;
`
const PointsElement = styled.div`
	font-size: 25px;
`
const Points = styled.strong`
	color: var(--color-decorative);
	font-size: 35px;
`
const Equation = styled.div`
	max-width: 400px;
	width: 100vw;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
`
const Number = styled.strong`
	color: var(--color-main);
	margin: 10px;
`
const Sign = styled.b``
const Input = styled.input`
	height: 50px;
	width: 80px;
	background-color: transparent;
	border: none;
	border-bottom: 5px solid var(--color-main);
	stroke-linecap: round;
	border-radius: 7px;
	text-align: center;
	color: var(--color-primary);
	font-size: 30px;
	font-family: 'Baloo';
	font-weight: bold;
	outline: none;
`
const Answers = styled.div`
	width: 90vw;
	max-width: 250px;
	display: grid;
	grid-template-columns: 1fr 1fr;
`
const fade = keyframes`
	from {
		opacity: 0;
		transform: scale(0.9);
	}
	to {
		opacity: 1;
		transform: scale(1);
	}
`
const Answer = styled.div`
	text-align: center;
	margin: 10px;
	padding: 5px;
	border: 2px solid var(--color-main);
	border-radius: 20px;
	transition: all 0.2s ease;
	animation: ${fade} 0.3s ease;
	:hover {
		background-color: var(--color-main);
		cursor: pointer;
		color: var(--color-bg);
	}
`
const rewardAnimation = keyframes`
	0% {
		transform: translate(-50%, -50%) scale(0.5);
		opacity: 0;
	}
	50% {
		transform: translate(-50%, -80px) scale(1);
		opacity: 1;
	}
	100% {
		transform: translate(40px, -50vh) scale(0);
		opacity: 0;
	}
`
const Reward = styled.div`
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	display: none;
	justify-content: center;
	align-items: center;
	width: 50px;
	height: 50px;
	background-color: var(--color-decorative);
	border-radius: 100%;
	color: var(--color-bg);
	animation: ${rewardAnimation} 0.8s both;
	${props =>
		props.preview &&
		css`
			display: flex;
		`
	};
`
const rotate = keyframes`
	from {
		transform: rotate(-30deg);
		opacity: 0;
	}
	to {
		transform: rotate(0);
		opacity: 1;
	}
`
const SettingsButton = styled(MdSettings)`
	animation: ${rotate} 0.5s 0.3s ease;
	transition: all 0.3s ease;
	:hover {
		cursor: pointer;
		opacity: 0.8;
		transform: scale(0.95) rotate(20deg);
	}
`
class CalcApp extends Component {
	state = {
		firstNumber: null,
		secondNumber: null,
		result: null,
		maxValue: 100,
		sign: '+',
		good: false,
		showSettings: false,
		disabled: [],
		answers: null,
		hideAd: false,
		userId: null,
		isLoggedIn: false,
		config: {
			negativeNumbers: false,
			game: 'addition',
			level: 'easy'
		},
		points: null
	}
	componentDidMount() {
		this.randomNumbers();
		this.setAnswers();
		au.onAuthStateChanged(user => {
			if (user) {
				this.setState({isLoggedIn: true})
				console.log(user);
				this.setState({userId: user.uid})
				db.collection('users').doc(user.uid).onSnapshot(snap => {
					const dataApp = snap.data()['easy-calc'];
					this.setState({
						points: dataApp.points,
						config: dataApp.config
					})
				})
				db.collection('users').get().then(snaps => {
					snaps.forEach(snap => {
						db.collection('users').doc(snap.data().uid).update({
							'easy-calc.config': {
								level: 'easy',
								game: 'addition',
								negativeNumbers: true
							}
						})
					})
				})
			}
			else {
				console.log('not logged in');
				this.setState({
					points: {
						addition: {
							easy: 0,
							hard: 0,
							medium: 0
						},
						division: {
							easy: 0,
							hard: 0,
							medium: 0
						},
						multiplication: {
							easy: 0,
							hard: 0,
							medium: 0
						},
						subtraction: {
							easy: 0,
							hard: 0,
							medium: 0
						}
					},
					config: {
						negativeNumbers: false,
						game: 'addition',
						level: 'easy'
					},
				})
			}
		})
	}
	randomNumbers = () => {
		this.setState({answers: null})
		let firstNumber = 0;
		let freeSpace = 0;
		if (this.state.config.level === 'easy') {
			if (this.state.sign === '+' || this.state.sign === '-') {
				firstNumber = Math.ceil((Math.random() - 0.5) * (this.state.maxValue / 5));
				freeSpace = this.state.maxValue / 5;
			}
			else if (this.state.sign === '*' || this.state.sign === '/') {
				firstNumber = Math.ceil((Math.random() - 0.5) * (this.state.maxValue / 10));
				freeSpace = this.state.maxValue / 10;
			}
			// console.log(freeSpace);
		}
		else if (this.state.config.level === 'medium') {
			if (this.state.sign === '+' || this.state.sign === '-') {
				firstNumber = Math.ceil((Math.random() - 0.5) * this.state.maxValue);
				freeSpace = this.state.maxValue;
			}
			else if (this.state.sign === '*' || this.state.sign === '/') {
				firstNumber = Math.ceil((Math.random() - 0.5) * (this.state.maxValue / 10));
				freeSpace = this.state.maxValue / 10;
			}
			// console.log(freeSpace);
		}
		else if (this.state.config.level === 'hard') {
			if (this.state.sign === '+' || this.state.sign === '-') {
				firstNumber = Math.ceil((Math.random() - 0.5) * (this.state.maxValue - firstNumber) * 2);
				freeSpace = this.state.maxValue - firstNumber;
			}
			else if (this.state.sign === '*' || this.state.sign === '/') {
				firstNumber = Math.ceil((Math.random() - 0.5) * (this.state.maxValue / 5));
				freeSpace = this.state.maxValue / 5;
			}
			// console.log(freeSpace);
		}
		// console.log(freeSpace);
		const secondNumber = Math.ceil((Math.random() - 0.5) * freeSpace);
		const result = eval(`firstNumber ${this.state.sign} secondNumber`);
		this.setState(() => {
			return {
				firstNumber,
				secondNumber,
				result
			};
		},
		() => {
			this.setAnswers();
		});
	}
	setAnswers = () => {
		const snare = [-10, 10, -1, 1, -2, 2, -5, 5]
		const ran1 = Math.floor(Math.random() * snare.length)
		const random1 = snare[ran1];
		const ran2 = Math.floor(Math.random() * (snare.length - 1))
		const random2 = snare.filter(x => x !== random1)[ran2];
		const ran3 = Math.floor(Math.random() * (snare.length - 2))
		const random3 = snare.filter(x => x !== random1 && x !== random2)[ran3];
		const answers = [
			this.state.result,
			this.state.result + random1,
			this.state.result + random2,
			this.state.result + random3
		];
		this.setState({answers: shuffle(answers)})
	}
	check = (e) => {
		// console.log(!this.state.disabled.includes(e.target.id));
		if (!this.state.disabled.includes(e.target.id)) {
			if(parseInt(e.target.value) === this.state.result || parseInt(e.target.id) === this.state.result) {
				this.setState({good: true});
				setTimeout(() => {
					this.randomNumbers();
					document.getElementById('input').value = '';
					setTimeout(() => {
						this.setState({
							good: false,
							disabled: []
						});
						if (this.state.userId) {
							db.collection('users').doc(this.state.userId).update({
								[`easy-calc.points.${this.state.config.game}.${this.state.config.level}`]: this.state.points[this.state.config.game][this.state.config.level] + 1
							})
							// console.log(this.state.points[this.state.game][this.state.level]);
						}
						else {
							this.setState(prevState => ({
								points: {
									...prevState.points,
									[this.state.config.game]: {
										...prevState.points[this.state.config.game],
										[this.state.config.level]: this.state.points[this.state.config.game][this.state.config.level] + 1
									}
								}
							}))
						}
					}, 400)
				}, 400)
			}
			else {
				if (!isNaN(parseInt(e.target.id))) {
					document.getElementById(e.target.id).style.opacity = 0;
					document.getElementById(e.target.id).style.cursor = "default";
					this.setState({disabled: this.state.disabled.concat(e.target.id)})
				}
				if(this.state.points[this.state.config.game][this.state.config.level] > 0) {
					this.setState({points: this.state.points - 1})
					if (this.state.userId) {
						db.collection('users').doc(this.state.userId).update({
							[`easy-calc.points.${this.state.config.game}.${this.state.config.level}`]: this.state.points[this.state.config.game][this.state.config.level] - 1
						})
					}
					else {
						this.setState(prevState => ({
							points: {
								...prevState.points,
								[this.state.config.game]: {
									...prevState.points[this.state.config.game],
									[this.state.config.level]: this.state.points[this.state.config.game][this.state.config.level] - 1
								}
							}
						}))
					}
				}
			}
		}
		else {
			console.log("Nie klikaj!");
		}
	}
	setLevel = () => {

	}
	setActivity = () => {

	}
	logIn = () => {
		this.props.history.push(`/login`);
	}
	closeAd = () => {
		this.setState({hideAd: true})
	}
	handleSettings = () => {
		!this.state.showSettings ? this.setState({showSettings: true}) : this.setState({showSettings: false})
	}
	render() {
		let vh = window.innerHeight * 0.01;
		document.documentElement.style.setProperty('--vh', `${vh}px`);
		window.addEventListener('resize', () => {
			let vh = window.innerHeight * 0.01;
			document.documentElement.style.setProperty('--vh', `${vh}px`);
		});
		return (
			<CalcAppComponent>
				<Preloader />
				<PointsElement>Punkty: <Points>{!this.state.points ? '-' : this.state.points[this.state.config.game][this.state.config.level]}</Points></PointsElement>
				<Equation>
					<Number>{this.state.firstNumber}</Number>
					<Sign>{this.state.sign}</Sign>
					<Number>{!this.state.secondNumber ? '0' : this.state.secondNumber.toString().replace(/-[0-9]+/g, x => `(${x})`)}</Number>
					{/* <Number>{!this.state.secondNumber ? '0' : `${this.state.secondNumber}`}</Number> */}
					<Sign>=</Sign>
					<Input readOnly={true} id="input" onChange={this.check} />
				</Equation>
				<Answers>
					{!this.state.answers ? '...' : this.state.answers.map(answer => {
						return (
							<Answer id={answer} key={answer} onClick={this.check}>{answer}</Answer>
						)
					})}
				</Answers>
				<div>
					{/* <Number>{this.state.result}</Number> */}
					<SettingsButton onClick={this.handleSettings} />
					{/* <button onClick={this.randomNumbers}>choose</button> */}
				</div>
				<Reward preview={this.state.good}></Reward>
				<Settings show={this.state.showSettings} onBack={this.handleSettings} />
				{this.state.isLoggedIn ? '' : <LoginAd onClick={this.logIn} onBack={this.closeAd} hide={this.state.hideAd} />}
			</CalcAppComponent>
		);
	}
}

// 

export default CalcApp